.making-section {
  background-color: $section-bgc;

  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;

  @media screen and(max-width: $to-tablet) {
    padding-top: 125px;
    padding-bottom: 97px;

    background-image: url(/images/making-img/mobile/milk-background@1x.png);

    @media screen and (min-resolution: 2dppx) {
      background-image: url(/images/making-img/mobile/milk-background@2x.png);
    }
  }

  @media screen and(min-width: $tablet) {
    padding-top: 130px;
    padding-bottom: 142px;
  }

  @media screen and(min-width: $desctop) {
    padding-top: 125px;
    padding-bottom: 139px;

    background-image: url(/images/making-img/desktop/milk-background@1x.png);

    @media screen and (min-resolution: 2dppx) {
      background-image: url(/images/making-img/desktop/milk-background@2x.png);
    }
  }
}

.section__title {
  margin-bottom: 0;
  margin-top: 15px;
}

.making-picture {
  @media screen and (max-width: $to-tablet) {
    margin-top: 26px;
  }
  @media screen and (min-width: $tablet) {
    width: 340px;
  }
  @media screen and (min-width: $desctop) {
    width: 516px;
  }
}

.flex-container {
  @media screen and (min-width: $tablet) {
    display: flex;
    margin-top: 50px;
  }
  @media screen and (min-width: $desctop) {
    margin-top: 55px;
    justify-content: space-between;
  }
}

.making-describe {
  @media screen and (max-width: $to-tablet) {
    padding-top: 46px;
    padding-bottom: 39px;
    padding-right: 30px;
    padding-left: 30px;
  }
  @media screen and (min-width: $tablet) {
    margin-top: 50px;
    margin-left: 17px;
    width: 343px;
  }
  @media screen and (min-width: $desctop) {
    width: 463px;
    margin-top: 60px;
  }
}

.making-describe p {
  font-weight: 500;
  font-style: normal;
  text-align: left;

  @media screen and (max-width: $to-tablet) {
    font-size: 12px;
    line-height: 1.58;
    letter-spacing: 0;
  }

  @media screen and (min-width: $tablet) {
    font-size: 14px;
    line-height: 1.79;
  }
  @media screen and (min-width: $desctop) {
    line-height: 1.93;
  }
}

.making-describe .making-begin {
  color: #000000;
  height: 79px;
  display: block;
  @media screen and (min-width: $tablet) and (max-width: $to-desctop) {
    letter-spacing: 0;
  }
}

.making-describe .making-middle {
  margin-top: 47px;
  color: $description-text;
  letter-spacing: 0, 04em;
  @media screen and (min-width: $tablet) {
    margin-top: 49px;
  }
  @media screen and(min-width: $desctop) {
    margin-top: 30px;
  }
}

.making-describe .making-result {
  margin-top: 15px;
  color: $description-text;
  @media screen and(min-width: $desctop) {
    margin-top: 30px;
  }
}

.making-button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 20px;
  padding: 0 37px;

  width: 176px;
  height: 44px;
  background-color: #ffffff;
  border-radius: 22px;
  border: none;

  font-size: 14px;
  font-weight: 700;
  line-height: 1.29;
  color: $accented-text;

  @media screen and (max-width: $to-tablet) {
    margin-top: 20px;
  }

  @media screen and (min-width: $tablet) {
    margin-top: 55px;
  }

  @media screen and(min-width: $desctop) {
    margin-top: 40px;
  }

  transition-property: background-color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  &:hover {
    background-color: $light-btn-hover-bgc;
  }
}

.making-button::after {
  content: '';
  width: 7px;
  height: 11px;
  display: inline-block;
  background-image: url(/images/making-buttton-arrow.svg);
  background-repeat: no-repeat;
  background-size: 7px 11px;
  background-position: 50% 50%;
}
.making-statistical {
  @media screen and(min-width: $tablet) {
    display: flex;
    margin-top: 64px;
  }
  @media screen and(min-width: $desctop) {
    margin-top: 39px;
  }
}

.li-title,
.li-description {
  margin: 0;
}

.li-title {
  font-family: Titan One;
  font-style: normal;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 0.04em;
  color: $accented-text;

  @media screen and (max-width: $to-tablet) {
    font-size: 36px;
    line-height: 1.14;
  }

  @media screen and (min-width: $tablet) {
    font-size: 48px;
    line-height: 1.15;
  }

  @media screen and(min-width: $desctop) {
    font-size: 58px;
    line-height: 1.14;
  }
}

.making-volume .li-title::before {
  content: '';
  width: 33px;
  height: 50px;
  display: block;
  background-image: url(/images/bucket-milk.svg);
  background-size: contain;

  @media screen and (max-width: $to-tablet) {
    margin-bottom: 16px;
  }

  @media screen and (min-width: $tablet) {
    margin-bottom: 36px;
  }

  @media screen and(min-width: $desctop) {
    margin-bottom: 40px;
  }
}

.making-weight .li-title::before {
  content: '';
  width: 50px;
  height: 50px;
  display: block;
  background-image: url(/images/bucket-apple.svg);
  background-size: contain;

  @media screen and (max-width: $to-tablet) {
    margin-bottom: 16px;
  }

  @media screen and (min-width: $tablet) {
    margin-bottom: 36px;
  }

  @media screen and(min-width: $desctop) {
    margin-bottom: 40px;
  }
}

.making-number .li-title::before {
  content: '';
  width: 44px;
  height: 50px;
  display: block;
  background-image: url(/images/sweets.svg);
  background-size: contain;

  @media screen and (max-width: $to-tablet) {
    margin-bottom: 16px;
  }

  @media screen and (min-width: $tablet) {
    margin-bottom: 36px;
  }

  @media screen and(min-width: $desctop) {
    margin-bottom: 40px;
  }
}

.li-description {
  margin-top: 20px;
  font-weight: 700;
  color: #000000;

  @media screen and (max-width: $to-tablet) {
    font-size: 12px;
    line-height: 1.67;
  }

  @media screen and (min-width: $tablet) {
    font-size: 14px;
    line-height: 1.71;
  }

  @media screen and(min-width: $desctop) {
    font-size: 16px;
    line-height: 1.75;
    letter-spacing: 0.02em;
  }
}

.making-statistical li + li {
  @media screen and(max-width: $to-tablet) {
    margin-top: 38px;
  }

  @media screen and(min-width: $tablet) {
    margin-left: 60px;
  }

  @media screen and(min-width: $desctop) {
    margin-left: 30px;
  }
}

