.container-prod {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 121px;
  padding-bottom: 98px;
  @media screen and (max-width: $to-mobile) {
    width: 100%;
    }
    @media screen and(min-width: $mobile) and (max-width: $to-tablet){
         max-width: $mobile;
    }
    @media screen and(min-width: $tablet) and (max-width:1279px){
     max-width: $tablet;
     padding-top: 122px;
    padding-left: 34px;
    padding-right: 34px;
    padding-bottom: 112px;
    }
    @media screen and (min-width: 1280px){
    width: 1280px;
    padding-top: 127px;
    padding-left: 116px;
    padding-right: 116px;
    padding-bottom: 119px;
    }
}
.product {
  position: relative;
  &:first-child {
    background-color: $hero-bgc;
  }
  &:nth-child(2) {
    background-color: $coffee;
  }
  &:last-child {
    background-color: $shakes;
  }
  border-radius: 24px;
  text-align: center;
  @media screen and (max-width: $to-tablet) {
    width: 280px;
    padding-bottom: 41px;
    padding-top: 161px;
    padding-left: 40px;
    padding-right: 40px;
    &:not(:last-child) {
      margin-bottom: 130px;
    }
    }
    @media screen and(min-width: $tablet) and (max-width:1279px){
     width: 220px;
     padding-bottom: 40px;
     padding-top: 152px;
     padding-left: 11px;
     padding-right: 11px;
    }
     @media screen and (min-width: 1280px) {
      width: 330px;
      padding-bottom: 50px;
      padding-top: 163px;
      padding-left: 44px;
      padding-right: 44px;
 }
}
.products .section-subtitle {
  font-family: Titan One;
  text-transform: uppercase;
  font-style: normal;
  font-weight: normal;
  @media screen and(max-width: $to-tablet) {
    font-size: 18px;
    line-height: 21px;
    }

  @media screen and(min-width: $tablet) and(max-width: 1279px){
    font-size: 26px;
    line-height: 30px;
  }
  @media screen and(min-width: 1280px) {
    font-size: 30px;
    line-height: 34px;
    letter-spacing: 0.04em;
    }}
.products .section__title {
  @media screen and(max-width: $to-tablet) {
    margin-top: 15px;
   margin-bottom: 144px;
   font-size: 34px;
   line-height: 39px;
     letter-spacing: 0.04em;}
 
   @media screen and(min-width: $tablet) and(max-width: 1279px){
     margin-top: 15px;
     margin-bottom: 150px;
     font-size: 48px;
     line-height: 55px;
     letter-spacing: 0.04em;
     
   }
   @media screen and(min-width: 1280px) {
     margin-top: 15px;
     margin-bottom: 259px;
     font-size: 58px;
     line-height: 66px;
     letter-spacing: 0.06em;    
 }
}
.products-list {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  @media screen and(min-width: $tablet){
    display: flex;
    justify-content: space-between;    
}
}
.product-name {
  font-family: Titan One;
  font-size: 20px;
  line-height: 1.15;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  margin-top: 0;
  margin-bottom: 0;
  @media screen and(min-width: 1280px){
    font-size: 30px;
    line-height: 34px;
    letter-spacing: 0.04em;
  }

}
.products-separator {
  width: 26px;
  display: flex;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
}
.separation-circle {
  display: inline-block;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: #fff;
}
.product-description {
  display: block;
  font-weight: 700;
  font-size: 14px;
  line-height: 156%;
  letter-spacing: 0.04em;
  text-align: center;
  color: #ffffff;
  @media screen and(max-width: $to-tablet) {
   margin-top: 30px;
  margin-bottom: 30px;}

  @media screen and(min-width: $tablet) and(max-width: 1279px){
    margin-top: 30px;
    margin-bottom: 40px;
    height: 124px;
    line-height: 176%;
    overflow: hidden;
  }
  @media screen and(min-width: 1280px) {
    margin-top: 30px;
    margin-bottom: 62px;
    height: 106px;
    font-size: 16px;
    line-height: 176%;
    overflow: hidden;    
}
}
.product-image__container {
  position: absolute;
  top: -116px;
  left: 16px;
  @media screen and(min-width: $tablet) and(max-width: 1279px){
  width: 100%;
  left: 0;
  top: -100px;
  height: 224;
  }
  @media screen and(min-width: 1280px){
    width: 100%;
    left: 0;
    top: -200px;
    height: 335;
  }
  }

.product-btn {
  border: none;
  padding: 0;
  background-color: #fff;
  border-radius: 50%;
  width: 40px;
  height: 40px;
   }
